




































import { Component, Vue, Provide } from 'vue-property-decorator'
import { UsertStore } from '@/data/User'

@Component<Nav>({
  components: {
  }
})
export default class Nav extends Vue {
  get Account () { return UsertStore.localState.Account }
  get Balances () { return UsertStore.localState.Balances }
  Login () {
    Vue.Modal.UserLogin()
  }
  Logout () {
    UsertStore.UserLogout()
  }
}
