
import UserLoginVue from '@/components/User/LoginDialog.vue'
import { CombinedVueInstance } from 'vue/types/vue'
import { MessageBox } from 'element-ui'
import { ModalData } from '@/types/Dialog'

export function ModalUserLogin (
  this: CombinedVueInstance<any, any, any, any, any>
) {
  const h = this.$createElement
  const ModalData: ModalData = {
    // 定义默认值，内部可以修改
    beforeClose (action, instance, done) {
      done()
    }
  }
  MessageBox({
    title: '登录',
    message: h(UserLoginVue, {
      props: { ModalData },
      name: 'UserLoginDialog',
      key: Date.now().toString()
    }, false),
    showCancelButton: true,
    confirmButtonText: '确定',
    cancelButtonText: '取消',
    beforeClose: (a, b, c) => ModalData.beforeClose(a, b, c)
  })
}
