import Data from '@/lib/data'
import * as fractal from 'fractal-web3'

class Store extends Data {
  public readonly state = {
  }

  public readonly localState: {
  } = {
  }

  protected name = `Fractal`

  constructor () {
    super()
    this.initilization()
  }

  public async GetInfoByAssetName (name: string) {
    return Promise.all([
      fractal.account.getAssetInfoByName(name).catch((e: any) => Promise.resolve(e))
    ])
  }

  public async GetInfoByAssetId (id: number) {
    return fractal.account.getAssetInfoById(id).catch((e: any) => Promise.resolve(e))
  }

  public async GetInfoByName (name: string) {
    return fractal.account.getAccountExByName(name).catch((e: any) => Promise.resolve(e))
  }

  public GetInfoByHash (hash: string) {
    return Promise.all([
      fractal.ft.getTransactionByHash(hash).catch((e: any) => Promise.resolve(e)),
      fractal.ft.getInternalTxByHash(hash).catch((e: any) => Promise.resolve(e)),
      fractal.ft.getBlockByHash(hash, true).catch((e: any) => Promise.resolve(e))
    ])
  }
}

export const FractalStore = new Store()
