










import { Component, Vue, Provide, Prop } from 'vue-property-decorator'
import { ModalData, ModalBeforeClose } from '@/types/Dialog'
import { MessageBoxCloseAction, ElMessageBoxComponent } from 'element-ui/types/message-box'
import { FractalStore } from '@/data/Fractal'
import { UsertStore } from '@/data/User'

@Component<UserLogin>({
  components: {
  }
})
export default class UserLogin extends Vue {
  @Prop({ type: Object }) ModalData!: ModalData;
  @Provide() Account = '';
  constructor () {
    super()
    this.ModalData.beforeClose = this.beforeClose.bind(this) // 先将该事件 hook
  }

  async beforeClose (action: MessageBoxCloseAction, instance: ElMessageBoxComponent, done: (() => void)) {
    if (action === 'confirm' && !await this.Submit()) return
    done()
  }

  async Submit () {
    const Account = this.Account.trim()
    if (!Account) return
    const res = await UsertStore.UserLogin(Account)
    if (typeof res === 'string') {
      this.$notify.error(res)
      return false
    }
    return true
  }
}
