
import Vue from 'vue'
import { ModalUserLogin } from '@/dialog/User'

const dialogRoot = new Vue({
  data () {
    return {}
  },
  el: document.createElement('div'),
  render: (h) => h('div')
})

class Modal {
  UserLogin = ModalUserLogin.bind(dialogRoot)
}

Vue.use((Vue) => {
  Vue.Modal = new Modal()
})

declare module 'vue/types/vue' {
  interface VueConstructor {
    Modal: Modal;
  }
}
