import Data from '@/lib/data'
import { Balance } from '@/types/Account'
import { FractalStore } from './Fractal'
import BigNumber from 'bignumber.js'

class Store extends Data {
  public readonly state = {
  }

  public readonly localState = {
    Account: '',
    Balances: [] as Balance[]
  }

  protected name = `User`

  constructor () {
    super()
    this.initilization()
  }

  async UserLogin (Account: string) {
    const res = await FractalStore.GetInfoByName(Account)
    if (!res) return 'account no exist'
    if (typeof res === 'string') return res

    this.localState.Balances = []
    this.localState.Account = Account
    if (res.balances) {
      const balances = await Promise.all(res.balances.map((balance: any) => FractalStore.GetInfoByAssetId(balance.assetID)))
      this.localState.Balances = balances.map((balance: any, index) => {
        const amount = res.balances[index].balance
        return {
          Value: parseFloat((new BigNumber(amount)).div(Math.pow(10, balance.decimals)).toFixed(4)),
          Id: balance.assetId,
          Name: balance.assetName,
          AssetName: balance.assetName,
          Owner: balance.owner,
          Founder: balance.founder,
          Symbol: balance.symbol.toLocaleUpperCase()
        }
      })
    }
    return true
  }

  UserLogout () {
    this.localState.Account = ''
    this.localState.Balances = []
  }
}

export const UsertStore = new Store()
